<template>
  <div :class="headClasses">
    <div :class="imgClasses"><slot name="img"></slot></div>
    <p :class="titleClasses"><slot></slot></p>
    <p :class="descClasses"><slot name="desc"></slot> </p>
  </div>
</template>
<script>
  const prefixCls = 'gtu-prices-schema'

  export default {
    name: 'price_schema_summary',
    computed: {
      headClasses () {
        return [`${prefixCls}-head`]
      },
      imgClasses () {
        return [`${prefixCls}-img`]
      },
      titleClasses () {
        return [`${prefixCls}-title`]
      },
      descClasses () {
        return [`${prefixCls}-desc`]
      }
    }
  }
</script>
<style lang="scss">
  $prefixCls: '.gtu-prices-schema';

  #{$prefixCls} {
    &-head {
      padding: 40px 15px;
      text-align: center;
      color: #fff;
      font-size: 14px;
    }
    &-img {
      max-width: 100%;
    }
    &-title {
      margin: 10px 0;
      font: {
        weight: bold;
        size: 18px;
      }
    }
  }
</style>


