<template>
  <div :class="bodyClasses">
    <div :class="priceClasses">
      <div>方案价格</div>
      <p>¥ <slot name="price"></slot></p>
      <button @click="handlePayment">立即购买</button>
    </div>
    <ul :class="funsClasses">
      <slot></slot>
    </ul>
  </div>
</template>

<script>
  const prefixCls = 'gtu-prices-schema'
  export default {
    name: 'price_schema_detail',
    computed: {
      bodyClasses () {
        return [`${prefixCls}-body`]
      },
      priceClasses () {
        return [`${prefixCls}-price`]
      },
      funsClasses () {
        return [`${prefixCls}-funs`]
      }
    },
    methods: {
      handlePayment () {
        this.$emit('on-payment')
      }
    }
  }
</script>
<style lang="scss">
  $prefixCls: '.gtu-prices-schema';

  #{$prefixCls} {
    &-body {
      padding: 15px;
    }
    &-price {
      border-bottom: 1px solid #ddd;
      div:first-child {
        line-height: 2;
        font: {
          weight: 400;
          size: 16px;
        }
      }
      p {
        text-indent: .5em;
        color: #999;
      }
      button {
        padding: 4px 16px;
        margin: 20px 0;
        border-radius: 3px;
        background-color: #fff;
        outline: 0;
        cursor: pointer;
      }
    }
    &-funs {
      display: flex;
      flex-wrap: wrap;
      padding: 10px 0;
    }
  }
</style>


