<template>
  <li :class="classes">
    <p><slot name="name"></slot></p>
    <p><slot name="desc"></slot></p>
  </li>
</template>

<script>
  const prefixCls = 'gtu-prices-schema-funs-item'
  export default {
    name: 'price_schema_functions',
    computed: {
      classes () {
        return [`${prefixCls}`]
      }
    }
  }
</script>

<style lang="scss">
  $prefixCls: ".gtu-prices-schema-funs-item";

  #{$prefixCls} {
    flex-basis: 50%;
    margin-bottom: 20px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    p:first-child {
      font: {
        weight: 400;
        size: 14px;
      }
      i {
        margin-right: 5px;
      }
    }
    p:last-child {
      color: #999;
    }
  }
</style>


