<template>
  <div :class="classes">
    <SchemaHead>
      <img slot="img" src="" />
      <template slot="desc">{{schemas[type].desc}}</template>
      {{schemas[type].type}}
    </SchemaHead>
    <SchemaBody @on-payment="handlePayment">
      <template slot="price">{{schemas[type].price}}</template>
      <template>
        <SchemaFunsItem v-for="(fun, i) in schemas[type].funs" :key="i">
          <template slot="name"><Icon :type="fun.icon"></Icon>{{fun.name}}</template>
          <template slot="desc">{{fun.desc}}</template>
        </SchemaFunsItem>
      </template>
    </SchemaBody>
  </div>
</template>

<script>
  import Icon from 'iview/src/components/icon'
  import Message from 'iview/src/components/message'

  const prefixCls = 'gtu-prices-schema'

  export default {
    name: 'price_schema',
    components: {
      Icon,
      SchemaHead: () => import(/* webpackChunkName:"prices" */'./SchemaSummary'),
      SchemaBody: () => import(/* webpackChunkName:"prices" */'./SchemaDetail'),
      SchemaFunsItem: () => import(/* webpackChunkName:"prices" */'./SchemaFunsItem')
    },
    props: {
      type: {
        validator (value) {
          var validList = ['free', 'standard', 'enterprise']
          for (let i = 0; i < validList.length; i++) {
            if (value === validList[i]) {
              return true
            }
          }
          return false
        }
      }
    },
    data () {
      return {
        schemas: {
          free: {
            type: '免费版',
            desc: '免费试用',
            price: 0,
            funs: [
              {
                name: '存储空间',
                icon: 'ios-bookmarks',
                desc: '50M总存储空间'
              },
              {
                name: '数据空间化',
                icon: 'gear-b',
                desc: '每月数据空间化50,000条'
              },
              {
                name: '数据调用',
                icon: 'arrow-graph-up-right',
                desc: '每月调用API接口50000次'
              }
            ]
          },
          standard: {
            type: '标准版',
            desc: '供一般用户使用',
            price: '299/月',
            funs: [
              {
                name: '存储空间',
                icon: 'ios-bookmarks',
                desc: '存储空间无限制'
              },
              {
                name: '数据空间化',
                icon: 'gear-b',
                desc: '每月数据空间化200000条'
              },
              {
                name: '数据调用',
                icon: 'arrow-graph-up-right',
                desc: '每月调用API接口200000次'
              }
            ]
          },
          enterprise: {
            type: '企业版',
            desc: '企业定制化方案',
            price: '?',
            funs: [
              {
                name: '存储空间',
                icon: 'ios-bookmarks',
                desc: '存储空间无限制'
              },
              {
                name: '数据空间化',
                icon: 'gear-b',
                desc: '每月更多的数据空间化配额'
              },
              {
                name: '数据调用',
                icon: 'arrow-graph-up-right',
                desc: '每月更多API接口调用'
              },
              {
                name: '专属服务',
                icon: 'map',
                desc: '使用更多类型的底图'
              }
            ]
          }
        }
      }
    },
    computed: {
      classes () {
        return [`${prefixCls}`, `${prefixCls}-${this.type}`]
      }
    },
    methods: {
      handlePayment () {
        // var types = this.type
        this.$Message.success(this.type)
        this.$http.get('/api/prices/buy', ' ').then((response) => {
          // 响应成功回调
          if (response.data.flag) {
            console.log('response:::' + response.data)
          }
          this.$Message.success('购买成功!')
        }, (response) => {
          // 响应错误回调
          if (response.status === 403) {
            this.$router.push({path: '/login'})
            return
          }
          this.$Message.error('购买失败!')
        })
      }
    },
    mounted () {
      this.$Message = Message
    }
  }
</script>

<style lang="scss">
  $prefixCls : '.gtu-prices-schema';
  $freeColor: #FCCC4B;
  $standardColor: #BBCF42;
  $enterpriseColor: #6395ED;

  #{$prefixCls} {
    border: 1px solid #fff;
    &-free {
      border-color: $freeColor;
      #{$prefixCls}-head {
        background-color: $freeColor;
      }
      #{$prefixCls}-price {
        button {
          border-color: $freeColor;
          color: $freeColor;
        }
      }
    }
    &-standard {
      border-color: $standardColor;
      #{$prefixCls}-head {
        background-color: $standardColor;
      }
      #{$prefixCls}-price {
        button {
          border-color: $standardColor;
          color: $standardColor;
        }
      }
    }
    &-enterprise {
      border-color: $enterpriseColor;
      #{$prefixCls}-head {
        background-color: $enterpriseColor;
      }
      #{$prefixCls}-price {
        button {
          border-color: $enterpriseColor;
          color: $enterpriseColor;
        }
      }
    }
  }
</style>


