<template>
  <div :class="classes">
    <div :class="ColClasses" v-for="(e, i) in editions" :key="i">
      <PriceSchema :type="e"></PriceSchema>
    </div>
  </div>
</template>

<script>
  const prefixCls = 'gtu-price'

  export default {
    name: 'prices',
    components: {
      PriceSchema: () => import(/* webpackChunkName: "prices" */ './PriceSchema')
    },
    data () {
      return {
        editions: ['free', 'standard', 'enterprise']
      }
    },
    computed: {
      classes () {
        return [`${prefixCls}`, 'gtu-container']
      },
      ColClasses () {
        return [`${prefixCls}-col`]
      }
    }
  }
</script>

<style lang="scss">
  $prefixCls: '.gtu-price';

  #{$prefixCls} {
    padding: 60px 0;
    display: flex;
    &-col {
      width: 33.3%;
      padding: 0 10px;
    }
  }
</style>


